igure,\nfigcaption,\nfooter,\nheader,\nhgroup,\nmenu,\nnav,\noutput,\nruby,\nsection,\nsummary,\ntime,\nmark,\naudio,\nvideo {\n    margin: 0;\n    padding: 0;\n    font-size: 100%;\n    font: inherit;\n    box-sizing: border-box;\n}\n\nol,\nul {\n    list-style: none;\n}\n","body {\n    font-family: montserrat, sans-serif;\n    src: url('https://fonts.googleapis.com/css2?family=Montserrat:wght@300;400;500;600;700&display=swap')\n        format('woff2');\n}\n","/* The switch - the box around the slider */\n.switch {\n    font-size: 17px;\n    position: relative;\n    display: inline-block;\n    width: 51px;\n    height: 24px;\n\n    @media screen and (min-width: 1200px) {\n        width: 53px;\n        height: 26px;\n    }\n\n    @media screen and (min-width: 1700px) {\n        width: 56px;\n        height: 29px;\n    }\n}\n\n/* Hide default HTML checkbox */\n.switch input {\n    opacity: 0;\n    width: 0;\n    height: 0;\n}\n\n/* The slider */\n.slider {\n    position: absolute;\n    cursor: pointer;\n    inset: 0;\n    border: 2px solid #414141;\n    border-radius: 50px;\n    transition: all 0.4s cubic-bezier(0.175, 0.885, 0.32, 1.275);\n    box-shadow: 0 0 20px rgba(9, 117, 241, 0.873);\n    border: 2px solid #0974f1;\n}\n\n.slider:before {\n    position: absolute;\n    content: '';\n    height: 15px;\n    width: 15px;\n    left: 0.2em;\n    bottom: 0.2em;\n    background-color: white;\n    border-radius: inherit;\n    transition: all 0.4s cubic-bezier(0.23, 1, 0.32, 1);\n\n    @media screen and (min-width: 1200px) {\n        height: 17px;\n        width: 17px;\n    }\n\n    @media screen and (min-width: 1700px) {\n        height: 20px;\n        width: 20px;\n    }\n}\n\n.switch input:checked + .slider {\n    box-shadow: 0 0 20px #f10909cc;\n    border: 2px solid #f10909;\n}\n\n.switch input:checked + .slider:before {\n    transform: translateX(1.5em);\n}\n","/* HTML: <div class=\"loader\"></div> */\n.loader__wrapper {\n    display: none;\n    position: fixed;\n    top: 50%;\n    left: 50%;\n    display: flex;\n    justify-content: center;\n    align-items: center;\n    width: 100%;\n    height: 100%;\n    transform: translate(-50%, -50%);\n    background: #00000099;\n    z-index: 99999;\n}\n\n.loader {\n    display: inline-flex;\n    gap: 10px;\n}\n\n.loader:before,\n.loader:after {\n    content: '';\n    height: 20px;\n    aspect-ratio: 1;\n    border-radius: 50%;\n    background: radial-gradient(farthest-side, #000 95%, #0000) 35% 35%/6px 6px no-repeat #fff;\n    transform: scaleX(var(--s, 1)) rotate(0deg);\n    animation: l6 1s infinite linear;\n}\n\n.loader:after {\n    --s: -1;\n    animation-delay: -0.1s;\n}\n\n@keyframes l6 {\n    100% {\n        transform: scaleX(var(--s, 1)) rotate(360deg);\n    }\n}\n","body {\n    position: relative;\n    display: flex;\n    flex-direction: column;\n    justify-content: space-between;\n    background-image: url('/src/assets/imgs/background.jpg');\n    background-size: cover;\n    height: 100vh;\n    padding: 26px;\n\n    @media screen and (min-width: 800px) {\n        padding: 48px;\n    }\n\n    @media screen and (min-width: 1200px) {\n        padding: 63px;\n    }\n\n    @media screen and (min-width: 1600px) {\n        padding: 80px;\n    }\n}\n\n// Adds black opacity overlay on top of the body background image\nbody::after {\n    content: '';\n    position: fixed;\n    top: 0;\n    left: 0;\n    width: 100%;\n    height: 100%;\n    background-color: rgba(0, 0, 0, 0.45);\n    z-index: 1;\n}\n\nh1,\nh3,\np,\nspan {\n    color: white;\n}\n\nmain,\n.container--left,\n.conatiner--right,\n.container--bottom {\n    position: relative;\n    z-index: 2;\n}\n\nmain {\n    display: flex;\n    justify-content: space-between;\n\n    @media screen and (min-width: 750px) {\n        font-size: 32px;\n        flex-wrap: wrap;\n    }\n}\n\n.container--left {\n    max-width: 55%;\n}\n\n.weather__conditions {\n    font-size: 25px;\n    margin-bottom: 7px;\n    font-weight: 500;\n    line-height: