.weather__units-text {\n    font-size: 12px;\n    margin-bottom: 10px;\n    flex-basis: 100%;\n\n    @media screen and (min-width: 1200px) {\n        font-size: 13px;\n    }\n\n    @media screen and (min-width: 1700px) {\n        font-size: 16px;\n    }\n}\n\n.celsius {\n    font-size: 13px;\n    margin-right: 5px;\n\n    @media screen and (min-width: 1200px) {\n        font-size: 14px;\n    }\n\n    @media screen and (min-width: 1700px) {\n        font-size: 16px;\n    }\n}\n\n.fahrenheit {\n    font-size: 13px;\n    margin-left: 5px;\n\n    @media screen and (min-width: 1200px) {\n        font-size: 14px;\n    }\n\n    @media screen and (min-width: 1700px) {\n        font-size: 16px;\n    }\n}\n\n.weather__img {\n    width: 64px;\n    margin-bottom: 9px;\n\n    @media screen and (min-width: 750px) {\n        font-size: 80px;\n        margin-bottom: 12px;\n    }\n\n    @media screen and (min-width: 1200px) {\n        font-size: 90px;\n        margin-bottom: 14px;\n    }\n\n    @media screen and (min-width: 1700px) {\n        width: 100px;\n        margin-bottom: 16px;\n    }\n}\n\n#search {\n    color: white;\n    font-size: 13px;\n    padding: 5px 35px 5px 10px;\n    width: 100%;\n    max-width: 219px;\n    background: none;\n    border: none;\n    border-bottom: 3px solid white;\n    outline: none;\n\n    @media screen and (min-width: 500px) {\n        font-size: 14px;\n    }\n}\n\n#search::placeholder {\n    color: white;\n    opacity: 0.55;\n}\n\n#searchBtn {\n    background: url('/src/assets/imgs/search-icon.png') no-repeat center center;\n    background-size: contain;\n    position: relative;\n    top: -27px;\n    right: -85%;\n    height: 19px;\n    width: 19px;\n    border: none;\n    cursor: pointer;\n\n    @media screen and (min-width: 460px) {\n        right: -85%;\n    }\n\n    @media screen and (min-width: 493px) {\n        top: -3px;\n        right: 36px;\n    }\n}\n\n.search__wrapper {\n    position: relative;\n}\n\n.error__msg {\n    display: none;\n    position: relative;\n    font-size: 10px;\n    color: red;\n    bottom: 0;\n    left: 2px;\n    margin-bottom: 3px;\n\n    @media screen and (min-width: 750px) {\n        font-size: 12px;\n        position: absolute;\n        min-width: 240px;\n        bottom: 39px;\n        margin-bottom: 0;\n    }\n\n    @media screen and (min-width: 750px) {\n        font-size: 14px;\n        position: absolute;\n        min-width: 240px;\n        bottom: 39px;\n        margin-bottom: 0;\n    }\n\n    @media screen and (min-width: 1200px) {\n        font-size: 15px;\n    }\n}\n\n.container--right {\n    display: flex;\n    flex-direction: column;\n    gap: 19px;\n    min-width: 95px;\n\n    @media screen and (min-width: 750px) {\n        gap: 24px;\n    }\n\n    @media screen and (min-width: 1200px) {\n        gap: 26px;\n    }\n\n    @media screen and (min-width: 1700px) {\n        gap: 28px;\n    }\n}\n\n.weather__feels-like,\n.weather__humidity,\n.weather__chance-of-precip,\n.weather__wind-speed {\n    display: flex;\n}\n\n.feels-like__icon-wrapper,\n.humidity__icon-wrapper,\n.chance-of-precip__icon-wrapper,\n.wind-speed__icon-wrapper {\n    margin-right: 10px;\n}\n\n.feels-like__title,\n.humidity__title,\n.chance-of-precip__title,\n.wind-speed__title {\n    font-size: 9px;\n    margin-bottom: 6px;\n    font-weight: 600;\n\n    @media screen and (min-width: 750px) {\n        font-size: 10px;\n    }\n\n    @media screen and (min-width: 1200px) {\n        font-size: 12px;\n    }\n\n    @media screen and (min-width: 1700px) {\n        font-size: 13px;\n    }\n}\n\n.feels-like__temperature,\n.humidity__percentage,\n.chance-of-precip__percentage,\n.wind-speed__speed {\n    font-size: 17px;\n    font-weight: 600;\n\n    @media screen and (min-width: 750px) {\n        font-size: 21px;\n    }\n\n    @media screen and (min-width: 1200px) {\n        font-size: 23px;\n    }\n\n    @media screen and (min-width: 1700px) {\n        font-size: 26px;\n    }\n}\n\n.feels-like__img,\n.humidity__img,\n.chance-of-precip__img,\n.wind-speed__img {\n    width: 19px;\n\n    @media screen and (min-width: 750px) {\n        width: 24px;\n    }\n\n    @media screen and (min-width: 1200px) {\n        width: 27px;\n    }\n\n    @media screen and (min-width: 1700px) {\n        width: 30px;\n    }\n}\n\n.container--bottom {\n    position: relative;\n    display: flex;\n    flex-wrap: wrap;\n    justify-content: center;\n    margin-top: 92px;\n\n    @media screen and (min-width: 700px) {\n        justify-content: space-between;\n        flex-wrap: nowrap;\n    }\n}\n\n.next-7-days {\n    position: absolute;\n    top: -63px;\n    left: 50%;\n    font-size: 15px;\n    border: 2px solid white;\n    padding: 6px 12px;\n    flex-basis: 100%;\n    margin-bottom: 30px;\n    transform: translateX(-50%);\n\n    @media screen and (min-width: 700px) {\n        left: 0;\n        transform: translateX(0);\n    }\n}\n\n.weather__monday,\n.weather__tuesday,\n.weather__wednesday,\n.weather__thursday,\n.weather__friday,\n.weather__saturday,\n.weather__sunday {\n    display: flex;\n    align-items: center;\n    width: 100%;\n    margin-bottom: 20px;\n\n    @media screen and (min-width: 350px) {\n        width: 85%;\n    }\n\n    @media screen and (min-width: 400px) {\n        width: 80%;\n    }\n\n    @media screen and (min-width: 450px) {\n        width: 70%;\n    }\n\n    @media screen and (min-width: 500px) {\n        width: 65%;\n    }\n\n    @media screen and (min-width: 550px) {\n        width: 60%;\n    }\n\n    @media screen and (min-width: 700px) {\n        display: initial;\n        align-items: unset;\n        margin-right: initial;\n        margin-bottom: 0;\n        width: 100%;\n    }\n}\n\n.monday,\n.tuesday,\n.wednesday,\n.thursday,\n.friday,\n.saturday,\n.sunday {\n    font-size: 15px;\n    margin-right: auto;\n    margin-bottom: 0;\n\n    @media screen and (min-width: 700px) {\n        margin-bottom: 20px;\n        margin-right: initial;\n    }\n\n    @media screen and (min-width: 1200px) {\n        font-size: 18px;\n    }\n\n    @media screen and (min-width: 1700px) {\n        font-size: 20px;\n    }\n}\n\n.monday__temperature,\n.tuesday__temperature,\n.wednesday__temperature,\n.thursday__temperature,\n.friday__temperature,\n.saturday__temperature,\n.sunday__temperature {\n    font-size: 21px;\n    font-weight: 700;\n    margin-right: 25px;\n\n    @media screen and (min-width: 700px) {\n        margin-right: initial;\n        margin-bottom: 20px;\n        margin-right: 0;\n    }\n\n    @media screen and (min-width: 750px) {\n        font-size: 24px;\n    }\n\n    @media screen and (min-width: 1200px) {\n        font-size: 26px;\n    }\n\n    @media screen and (min-width: 1700px) {\n        font-size: 28px;\n    }\n}\n\n.monday__img,\n.tuesday__img,\n.wednesday__img,\n.thursday__img,\n.friday__img,\n.saturday__img,\n.sunday__img {\n    width: 30px;\n\n    @media screen and (min-width: 750px) {\n        width: 36px;\n    }\n\n    @media screen and (min-width: 1200px) {\n        width: 42px;\n    }\n\n    @media screen and (min-width: 1700px) {\n        width: 65px;\n    }\n}\n\nmain,\n.container--bottom {\n    // Initially hides all content until the page is first loaded/populated\n    display: none;\n}\n\n.reveal {\n    display: flex;\n}\n\n.hide {\n    display: none;\n}\n"],"sourceRoot":""}]);
